   table {
    border-collapse: collapse;
    margin-top: 100px;
    width: 100%;
  } 
  
  th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  th {
    background-color: #f2f2f2;
  }
  
  .total {
    margin-top: 20px;
    width: 400px;
    border-radius: 5px;
    border: 2px solid black;
    font-size: 14px;
    padding: 10px;
    font-weight: 500;
    margin-left: 1000px;
  }
  .cancel-icon img {
 width: 20px;
 height: 20px; /* Adjust the font-size to make the icon smaller */
  }
  .input11 input{
    width: 33px;
    height:20px;
    padding: 5px;
    border: 2px solid black;
    align-items: right;
  }
  .total button{
    width: 210px;
    height: 45px;
    background-color: red;
    color: white;
    border: none;
    font-weight: 600;
    text-align: center;
    align-items: center;
    margin-left: 100px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
  }
  