.admin {
    display: flex;
    justify-content: space-between;
  }
  
  .admin-left {
    width: 250px;
    height: 100vh;
    background-color: #3f51b5;
    color: #fff;
    padding-top: 20px;
  }
  
  .admin-left h1 {
    cursor: pointer;
    font-size: 16px;
    margin: 10px 0 0 20px;
    font-weight: 500;
    margin-top: 30px;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .admin-left h1:hover {
    color: #ffc107;
    transform: scale(1.02);
  }
  
  .admin-right {
    flex: 1;
    padding: 20px;
  }
  .admin-right h2{
    text-align: center;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th, td {
    padding: 12px;
    text-align: left;
  }
  
  th {
    background-color: #3f51b5;
    color: #fff;
  }
  
  td {
    border-bottom: 1px solid #ccc;
  }
  
  tbody tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  tbody tr:hover {
    background-color: #e0e0e0;
  }
  .search-bar {
    margin-bottom: 10px;
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
    background-color: #f8f9fa;
  }
  
  .search-bar::placeholder {
    color: #999;
  }
  
  .search-bar:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }
  
  