.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: black;
  color: #fff;
  padding: 50px 20px;
}

.footer h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.footer h2 {
  font-size: 16px;
  margin: 10px 0;
}

.footer h3 {
  font-size: 14px;
  margin: 5px 0;
}

.footer input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #fff;
  border-radius: 5px;
}

.images11 {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.lfi img {
  width: 100px;
}

.rfi img {
  width: 120px;
  margin-left: 10px;
  display: flex;
}

.icons img {
  width: 30px;
  margin-right: 10px;
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 30px 10px;
  }
  
  .footer .f1 {
    flex: 1 0 50%;
    margin-bottom: 30px;
  }

  .footer .f1 h1 {
    font-size: 20px;
    margin-bottom: 15px;
  }

  .footer input[type="text"] {
    width: calc(100% - 20px);
  }

  .images11 {
    flex-direction: column;
    align-items: flex-start;
  }

  .lfi img {
    margin-bottom: 10px;
  }

  .rfi img {
    margin-left: 0;
  }
}

@media screen and (max-width: 480px) {
  .footer .f1 {
    flex: 1 0 100%;
  }

  .footer .f1 h1 {
    font-size: 18px;
  }

  .footer .f1 h2,
  .footer .f1 h3 {
    font-size: 14px;
  }

  .footer input[type="text"] {
    width: calc(100% - 20px);
  }

  .images11 {
    flex-direction: column;
    align-items: center;
  }
}
