.contact1
{
    display: flex;
}
.contact span{
    margin-left: 163px;
}
.left-contact {
    width: 400px;
    height: 500px;
    margin-top: 50px;
    margin-left: 165px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0.4, 0.3); /* Add shadow effect */
    border-radius: 7px;
}

.left-contact h1{
    font-size: 20px;  
    padding: 10px;
}
.left-contact h2{
    font-size: 17px;  
    margin-left: 10px;
    font-weight: 600;
}
.left-contact img{
   margin-left: 10px;
   margin-top: 10px;
}

.right-contact{
    width: 1100px;
    height: 500px;
    margin-top: 50px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0.4, 0.3); /* Add shadow effect */
    border-radius: 7px;
}
.right-contact input{
    width: 236px;
    height: 37px;
    border-radius: 3px;
    padding-left: 10px;
    margin: 30px;
    font-size: 17px;
}
.right-contact textarea{
    width: 87%;
    margin-left: 30px;
    height: 250px;
    padding: 20px;
    font-size: 17px;
}
.right-contact button{
    background-color: red;
    color: white;
    border-radius: 5px;
    width: 200px;
    height: 45px;
    font-size: 15px;
    font-weight: 600;
    border-color: white;
    margin-top: 30px;
    margin-left: 700px;
    cursor: pointer;
   
}