.profile {
    flex-direction: column;
    align-items: center;
    padding: 20px;
    height: 100%;
  } 

  .profile span{
    margin-left: 163px;
}
  
  .profiles {
    width: 400px;
    margin-top: 50px;
    padding: 30px;
    margin-left: 550px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
  }
  
  .profiles h3 {
    text-align: center;
    margin-bottom: 20px;
    color: red;
  }
  
  .profiles label {
    display: block;
    margin-bottom: 5px;
  }
  
  .profiles input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .profiles button {
    width: 100%;
    padding: 10px;
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .profiles button:hover {
    background-color: #0056b3;
  }
  .profiles button.save-button {
    background-color: red;
  }
  