.bill span{
    margin-left: 163px;
}

.bill{
    display: flex;
}

.bill-left {
    width: 500px;
    height: 700px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); 
    margin-top: 100px;
    margin-left: -200px;
  }

  .bill-left input{
    width: 310px;
    height: 36px;
    border: none;
    font-weight: 500;
    font-size: 14px;
    background-color: #d9d9d9;
    color: black;
    padding-left: 10px;
    margin-top: 25px;
    margin-left: 100px;
   
  }
  
  .bill-left h1{
    text-align: center;
    padding: 20px;
    font-size: 27px;
  }
  button{
    cursor: pointer;

  }

  #ch1{
    width: 20px;
    height: 20px;
    
  }
  #ch1 p{
    font-size: 12px;
  }
  .bill-left button{
    cursor: pointer;
    width: 290px;
    height: 35px;
    font-weight: 600;
    font-size: 14px;
    color: white;
    background-color: red;
    margin-left: 100px;
    border-radius: 3px;
    border: none;
    margin-top: 40px;
  }
  .bill-right {
    width: 600px;
    height: 1000px;
    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
    margin-top: 100px;
    margin-left: 150px;
   
  }
  .bill-right h1{
    text-align: center;
    padding: 20px;
    font-size: 27px;
  }
  
  .cart-items {
    display: flex;
    flex-wrap: wrap;
  
  }
  
  .cart-item {
    margin-right: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
  }
  
  .bill-right img {
    width: 50px; /* Adjust the size as needed */
    height: auto;
    margin-left: 20px;
    margin-bottom: 10px; /* Add margin to the bottom */
  }
  
  .item-details {
    margin-left: 10px; /* Add margin to the left */
  }

  .bill-right h3{
    margin-left: 30px;
  }
  .bill-right button{
    width: 210px;
    height: 45px;
    background-color: red;
    color: white;
    border: none;
    font-weight: 600;
    text-align: center;
    align-items: center;
    margin-left: 200px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
  }
  
  